// 
// login
export const loginRules = {

  mobile: [{
    required: true,
    message: '',
    trigger: 'blur',
    pattern: /^1[3|4|5|6|7|8|9]\d{9}$/
  }],
  code: [{
    required: true,
    message: '6',
    trigger: 'blur', // change 
    len: 6
  }],
  check: [{
    validator: (rule, value, callback) => {
      if (!value) {
        callback(new Error(''))
      } else {
        callback()
      }
    },
    trigger: 'change'
  }]

}
export const addArticleRules = {
  title: [{
    required: true,
    min: 5,
    max: 30,
    message: '5-30',
    trigger: 'blur'
  }],
  content: [
    {
      required: true,
      message: '',
      trigger: 'blur'
    }
  ],
  channel_id: [
    {
      required: true,
      trigger: 'blur',
      message: ''
    }
  ],
  cover: [
    {
      validator: (rule, value, callback) => {
        if (value.images.indexOf(undefined) > -1) {
          callback(new Error(''))
        }
        if (value.type === value.images.length || value.type === -1) {
          callback()
        } else {
          callback(new Error(''))
        }
      },
      required: true
    }
  ]
}
