<!-- // -->
<template>
  <div class="my-cover">
    <!--  -->
    <div class="btn_img" @click="openDialog()">
      <img :src="coverImageUrl" />
    </div>
    <!-- 
    append-to-bodytrue, body
    visible dialog(), sync
     -->
    <el-dialog
      :append-to-body="true"
      :visible.sync="dialogVisible"
      width="720px">
      <!-- tab -->
      <el-tabs v-model="activeName" type="card">
        <el-tab-pane label="" name="image">
                   <!--  -->
          <div style="padding-bottom: 20px">
            <el-radio-group  size="mini" v-model="radioType">
              <el-radio-button label=""></el-radio-button>
              <el-radio-button label=""></el-radio-button>
            </el-radio-group>
          </div>
          <!--  -->
          <!-- 
              :gutter  
              :xs <768px 
              :sm 768px 
              :md 992px 
              :lg 1200px 
             -->
          <el-row :gutter="10" class="img_list">
            <el-col
              class="img_item"
              :xs="12"
              :sm="6"
              :md="6"
              :lg="4"
               v-for="item in imgListArr"
              :key="item.id"
              @click.native="selectedFn(item)"
            >
              <!-- fit:cover -->
              <el-image
                style="height: 100px"
                :src="item.url"
                fit="cover"
                :class="{selected: selectImgUrl === item.url}"
              ></el-image>
            </el-col>
          </el-row>
          <!--  prev, next, pager, total -->
          <el-pagination
            background
            layout="prev, pager, next"
            :total="total"
            @current-change='changePage'
            :current-page = 'reqParmas.page'
          >
          </el-pagination>
        </el-tab-pane>

        <el-tab-pane label="" name="upload">
          <el-upload
                class="upload-demo"
                action="http://api-toutiao-web.itheima.net/mp/v1_0/user/images"
                :headers="headers"
                name='image'
                :show-file-list='false'
                :on-success="handleAvatarSuccess"
                @size-change="handleSizeChange"
                :before-upload="beforeAvatarUpload"
            >
               <img v-if="imageUrl" :src="imageUrl" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-tab-pane>
      </el-tabs>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click=" confirmFn"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { imgListAPI } from '@/api'
import defaultPng from '@/assets/default.png' // webpackassets, base64, 
// console.log(defaultPng)s
export default {
  name: 'MyCover',
  data () {
    return {
      coverImageUrl: defaultPng, // 
      // 
      dialogVisible: false,
      // tabsname
      activeName: 'image',
      radioType: '', // 
      reqParmas: {
        collect: false,
        page: 1,
        per_page: 8
      },
      total: 0,
      imgListArr: [], // 
      selectImgUrl: '', // url
      headers: {
        Authorization: 'Bearer ' + sessionStorage.getItem('token')
      },
      imageUrl: ''// 

    }
  },
  props: {
    index: Number,
    value: String
  },
  watch: {
    radioType (newVal) {
      this.reqParams.collect = !(newVal === '')
      this.reqParmas.page = 1
      // if (newVal === '') {
      //   this.reqParmas.collect = false
      // } else if (newVal === '') {
      //   this.reqParmas.collect = true
      // }
      this.getUserImgFn()
    },
    value: {
      immediate: true,
      handler (newVal) {
        if (newVal !== undefined) {
          this.coverImageUrl = newVal
        }
      }
    }
  },
  methods: {
    beforeAvatarUpload (file) {
      const isJPG = file.type === 'image/jpeg'
      const isLt2M = file.size / 1024 / 1024 < 2
      if (!isJPG) {
        this.$message.error(' JPG  / PNG !')
      }
      if (!isLt2M) {
        this.$message.error(' 2MB!')
      }
      return isJPG && isLt2M // true
    },
    handleAvatarSuccess (res, file) {
      // console.log(1)
      this.imageUrl = res.data.url
      this.$message.success('')
      this.getUserImgFn()
    },
    openDialog () {
      this.dialogVisible = true
      this.selectImgUrl = this.coverImageUrl
    },
    async getUserImgFn () {
      const [err, res] = await imgListAPI(this.reqParmas)
      if (err) return
      // console.log(res)
      this.imgListArr = res.data.data.results
      this.total = res.data.data.total_count
    },
    changePage (page) {
      this.reqParmas.page = page
      this.getUserImgFn()
    },
    selectedFn (obj) {
      if (this.selectImgUrl && this.selectImgUrl === obj.url) {
        this.selectImgUrl = defaultPng
      } else {
        this.selectImgUrl = obj.url
      }
    },
    confirmFn () {
      this.dialogVisible = false
      this.coverImageUrl = this.selectImgUrl
      if (this.coverImageUrl === defaultPng) {
        this.$emit('coverimg', false, this.index)
      } else {
        this.$emit('coverimg', this.coverImageUrl, this.index)
      }
    },
    handleSizeChange (val) {
      // console.log(val)
      this.reqParams.per_page = val
      this.getImgListFn()
    }
  },

  async created () {
    this.getUserImgFn()
  }
}
</script>

<style scoped lang='less'>
/*  */
.btn_img {
  width: 150px;
  height: 150px;
  border: 1px dashed #ddd;
  img {
    width: 100%;
    height: 100%;
    display: block;
  }
}
/*  */
.img_list {
  margin-top: 15px;
  .img_item {
    width: 150px;
    height: 120px;
    border: 1px dashed #ddd;
    display: inline-block;
    margin-right: 15px;
    img {
      width: 100%;
      height: 100%;
      display: block;
    }
  }
}
/*  */
.img_item {
  position: relative;
}
/*  */
.selected::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.3) url(../assets/selected.png) no-repeat center /
    50px 50px;
}
.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>
